export default {
	data() {
		return {
			flag:false,
			wechat: 'jayne_zou',
			jumpList:[
				{name:'联系我们',pathUrl:''},
				{name:'使用协议及隐私政策',pathUrl:'/pages/privacy/privacy'}
			]
		}
	},
	methods: {
		closeModal(){
			this.flag = false
		},
		copy() {
			var that = this;
			uni.setClipboardData({
				//准备复制的数据
				data: that.wechat,
				success: function(res) {
					wx.showToast({
						title: '复制成功',
					});
				}
			});
		},
		jump(url){
			if(url){
				uni.navigateTo({
					url
				})
			}else{
				this.flag = true
			}
		}
	}
}
